import { RouterTestingModule } from '@angular/router/testing';
import { Component, OnInit, } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

@Component({
  selector: 'app-custom-search',
  templateUrl: './custom-search.component.html',
  styleUrls: ['./custom-search.component.css']
})
export class CustomSearchComponent implements OnInit {

  constructor(private http: HttpClient) { }
  searchValue: any;
  result: any;
  resultTitle: any;
  resultContent: any;
  resultDisplayLink: any;
  resultFroamttedURL: any;
  showDownloadBtn = false;
  ngOnInit() {

  }

  searchData() {
    const url = 'https://www.googleapis.com/customsearch/v1?key=AIzaSyBFp65YeNVovFraK7oZHXxyD2Jn5zN0BZU&cx=005124428384360536924:rstfldysumw&q=' + this.searchValue + '&safe=high';
    console.log('my input', this.searchValue);
    this.http.get<any>(url).subscribe(data => {
      this.result = data.items;
      console.log('What is my result', this.result);
    });
    setTimeout(() => {
      this.showDownloadBtn = true;
    }, 900);
  }
}
